<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity .3s ease;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>

<template>
<div id="app">
  <navbar></navbar>
  <transition name="fade" mode="out-in">
    <router-view class="ui container"></router-view>
  </transition>
</div>
</template>

<script>
import Vue from 'vue'
import navbar from './components/navbar.vue'

Vue.component('navbar', navbar)

export default {
  name: 'app',
  components: {
    navbar
  },
  data() {
    return {}
  },
  mounted() {
    let redirect = window.sessionStorage.getItem('redirect')
    if (redirect) {
      let path = window.sessionStorage.getItem('path')
      this.$router.push(path)
      window.sessionStorage.clear();
    }
  }
}
</script>



