<template>
<div class="ui inverted blue centered segment block header">
  <h1>Spark Space:
        <span>{{msg}}</span>
      </h1>
  <p>Automatically Generated FAQ Page</p>
</div>
</template>

<script>
import Vue from 'vue';
import axios from 'axios';

let msg = '';

export default {
  name: 'banner',
  props: ['space'],
  computed: {
    apiUrl() {
      let path = 'detail/';
      let roomApi = this.$store.state.apiUrl + path + this.space;
      return roomApi;
    }
  },
  mounted: function() {
    this.setupCharts()
  },
  methods: {
    setupCharts() {
      axios.get(this.apiUrl, {
        withCredentials: true
      }).then(response => {
        this.msg = response.data.displayName
      });
    }
  },
  data() {
    return {
      msg: msg
    }
  }
}
</script>

